<template>
  <div class="CustomerDataDetails">
    <HeaderNavigation />
    <div class="CustomerDataDetailsBox">
      <h1>客戶資料</h1>
      <h2>Customer Information</h2>
      <div class="CustomerDataDetailsBoxData">
        <div class="CustomerDataDetailsBoxDataHeader">
          <img
            v-if="!personalDetails.CustPictureUrl"
            class="headerImg"
            draggable="false"
            src="@/assets/image/InstallHeadPortrait.png"
            alt=""
          />
          <img
            v-if="personalDetails.CustPictureUrl"
            class="headerImg"
            draggable="false"
            :src="personalDetails.CustPictureUrl"
            alt=""
          />
          <span>{{ personalDetails.Name }}</span>
          <img
            v-if="personalDetails.IsFromOpportunity"
            class="iconImg"
            draggable="false"
            src="@/assets/image/commercialOpportunity.png"
            alt=""
          />
          <div @click="toCustomerDataDetailsEdit()">編輯</div>
        </div>
        <p>點數：{{ personalDetails.Points }}</p>
        <p>地址：{{ personalDetails.Address }}</p>
        <p>電話：{{ personalDetails.Mobile }}</p>
        <em>{{ personalDetails.Remark }}</em>
      </div>
      <div
        class="CustomerDataDetailsBoxContent"
        v-for="(item, index) in dataFormList"
        :key="index"
      >
        <h3>
          {{ item.ModelTypeName }}
          <span class="label " v-if="item.MaintainFlag == '定期保養'">{{
            item.MaintainFlag
          }}</span>
          <span class="label diy" v-if="item.MaintainFlag == '自行DIY'">{{
            item.MaintainFlag
          }}</span>
          <em @click="toInstallCourse(item.CustProdId, item.MFNO)">保養歷程</em>
        </h3>
        <p>製造號碼：{{ item.MFNO }}</p>
        <p>安裝日期：{{ item.InstallDate }}</p>
        <p>濾心最新更換日：{{ item.Detail[0].LastChangeDate }}</p>
        <div>
          <span v-for="(items, index) in item.Detail" :key="index">
            <img
              draggable="false"
              src="@/assets/image/GovernmentServiceCenter.png"
              alt=""
            />
            <strong>{{ items.FilterDescription }}</strong>
            <em>{{ items.LastChangeDate }}</em>
          </span>
        </div>
        <div class="imgWrap">
          <div
            class="list"
            v-for="(itemss, indexa) in imageList[index]"
            :key="indexa"
          >
            <div class="img" @click="openBigImg(itemss.ImageUrl, itemss.Type)">
              <img :src="itemss.ImageUrl" />
            </div>
            <div class="txt">{{ itemss.Type }}</div>
          </div>
        </div>
      </div>
    </div>
    <van-popup v-model="beDefeatedShow" class="beDefeatedShowStyle">
      <div class="img-box">
        <img :src="bigImg" alt="" />
      </div>
      <h2>{{ tit }}</h2>
    </van-popup>
  </div>
</template>

<script>
import {
  POST_GetCustomerByDistributtion,
  POST_GetCustWaterProdList,
  POST_GetInstallData,
} from "@/utils/api";
import HeaderNavigation from "@/components/HeaderNavigation";
export default {
  name: "CustomerDataDetails",
  components: {
    HeaderNavigation,
  },
  data() {
    return {
      personalDetails: {},
      beDefeatedShow: false,
      bigImg: "",
      tit: "",
      imgList: require("@/assets/image/img1.jpg"),
      imgList2: require("@/assets/image/img1.jpg"),
      imgList3: require("@/assets/image/img1.jpg"),
      imgListTitle: "進水閥開關處",
      imgList2Title: "機體燈號",
      imgList3Title: "廚下安裝後照片",
      dataFormList: [
        // {
        //   orderId: "P0230",
        //   installationDate: "2019年02月14日",
        //   changeDate: "2019年08月23日",
        //   Name:"柯欣妤",
        //   ModelTypeName:"P0230",
        //   Points:"0",
        //   Mobile:"電話：0987303820",
        //   Address:"地址：103-台北市大同區南京西路４１號５F",
        //   tag:"自行DIY",
        //   Detail: [
        //     {
        //       id: "1333",
        //       imgUrl: require("@/assets/image/GovernmentServiceCenter.png"),
        //       FilterDescription: "複合式濾芯(活性碳+PP)",
        //       LastChangeDate: "2019.12.27"
        //     },
        //     {
        //       id: "54333",
        //       imgUrl: require("@/assets/image/GovernmentServiceCenter.png"),
        //       FilterDescription: "後置活性碳",
        //       LastChangeDate: "2020.01.27"
        //     },
        //     {
        //       id: "54233",
        //       imgUrl: require("@/assets/image/GovernmentServiceCenter.png"),
        //       FilterDescription: "RO膜(600G)",
        //       LastChangeDate: "2020.04.08"
        //     }
        //   ]
        // },
        // {
        //   orderId: "P0234",
        //   installationDate: "2019年02月14日",
        //   changeDate: "2019年08月23日",
        //   ModelTypeName:"P0230",
        //   tag:"定期保養",
        //   filterElementList: [
        //     {
        //       id: "1233",
        //       imgUrl: require("@/assets/image/GovernmentServiceCenter.png"),
        //       title: "濾心1號",
        //       date: "2019.12.27"
        //     },
        //     {
        //       id: "1243",
        //       imgUrl: require("@/assets/image/GovernmentServiceCenter.png"),
        //       title: "濾心2號",
        //       date: "2020.01.27"
        //     }
        //   ]
        // }
      ],
      imageList: [],
    };
  },
  created() {
    this.GetCustomerByDistributtion();
    this.GetCustWaterProdList();
  },
  methods: {
    openBigImg(value, tit) {
      this.beDefeatedShow = true;
      this.bigImg = value;
      this.tit = tit;
    },
    // 跳转至保养历程
    toInstallCourse(value, Mfno) {
      this.$router.push(
        "/CustomerService/CustomerData/CustomerDataDetails/CustomerDataDetailsHistory?CustId=" +
          value +
          "&CustPhone=" +
          this.$route.query.CustPhone +
          "&id=" +
          this.$route.query.id +
          "&Mfno=" +
          Mfno
      );
    },
    // 获取客户信息
    GetCustomerByDistributtion() {
      var arrays0 = this.$route.query.CustPhone.split("-")[0];
      var arrays1 = this.$route.query.CustPhone.split("-")[1];
      var UofIds = {
        CustPhone: arrays0 + arrays1,
        DistributionUofId: sessionStorage.getItem("DistributionUofId"),
      };
      POST_GetCustomerByDistributtion(UofIds).then((response) => {
        this.personalDetails = response.Data;
      });
    },

    GetCustWaterProdList() {
      var UofIds = {
        CustId: this.$route.query.id,
        DistributionUofId: sessionStorage.getItem("DistributionUofId")
      };
      POST_GetCustWaterProdList(UofIds).then((response) => {
        this.dataFormList = response.Data;
        for (let i = 0; i < response.Data.length; i++) {
          var form = {
            Mfno: response.Data[i].MFNO,
          };
          // 获取安装图片
          POST_GetInstallData(form).then((response) => {
            // this.imageList.push(response.Data.ImageList);
            this.imageList.splice(i, 0, response.Data.ImageList);
          });
        }
      });
    },

    toCustomerDataDetailsEdit() {
      this.$router.push(
        "/CustomerService/CustomerData/CustomerDataDetails/CustomerDataDetailsEdit?id=" +
          this.$route.query.id +
          "&Remark=" +
          this.personalDetails.Remark
      );
    },
  },
};
</script>
<style lang="scss">
.CustomerDataDetails {
  .van-overlay {
    background: #000;
    z-index: 99999;
  }
}
</style>
<style lang="scss" scoped>
.van-popup--center {
  top: 50%;
  left: 0;
  -webkit-transform: translate3d(0%, -50%, 0);
  transform: translate3d(0%, -50%, 0);
  width: 100%;
  overflow: inherit;
  z-index: 99999 !important;
  .img-box {
    max-height: 375px;
    height: 375px;
    overflow: hidden;
    img {
      width: 100%;
      height: 100%;
      object-fit: contain;
    }
  }
  .img-box + h2 {
    font-size: 15px;
    font-weight: 500;
    color: hsl(0, 0%, 100%);
    line-height: 21px;
    position: fixed;
    bottom: -43px;
    padding: 0 20px;
  }
}
.CustomerDataDetails {
  width: 100%;
  .CustomerDataDetailsBox {
    padding: 30px 5.33% 0;
    h1 {
      font-size: 22px;
      font-weight: 500;
      color: rgba(61, 61, 61, 1);
      line-height: 30px;
    }
    h2 {
      font-size: 12px;
      font-weight: 500;
      color: rgba(134, 134, 134, 1);
      line-height: 17px;
    }
    .CustomerDataDetailsBoxData {
      margin-top: 20px;
      margin-bottom: 10px;
      padding: 16px 5.37%;
      background: rgba(255, 255, 255, 1);
      border-radius: 8px;
      .CustomerDataDetailsBoxDataHeader {
        display: flex;
        margin-bottom: 15px;
        .headerImg {
          height: 30px;
          width: 30px;
          border-radius: 50%;
        }
        span {
          font-size: 16px;
          font-weight: 500;
          color: rgba(61, 61, 61, 1);
          line-height: 22px;
          margin: 0 8px;
        }
        .iconImg {
          width: 62px;
          height: 17px;
        }
        div {
          margin-left: auto;
          width: 66px;
          height: 30px;
          background: linear-gradient(
            180deg,
            rgba(28, 118, 254, 1) 0%,
            rgba(28, 118, 254, 1) 100%
          );
          box-shadow: 0px 6px 24px 0px rgba(28, 118, 254, 0.3);
          border-radius: 6px;
          font-size: 14px;
          font-weight: 500;
          color: rgba(255, 255, 255, 1);
          line-height: 30px;
          text-align: center;
        }
      }
      p {
        font-size: 14px;
        font-weight: 500;
        color: rgba(61, 61, 61, 1);
        line-height: 20px;
      }
      em {
        display: block;
        padding-top: 12px;
        font-size: 12px;
        font-weight: 500;
        color: rgba(189, 189, 189, 1);
        line-height: 18px;
      }
    }
    .CustomerDataDetailsBoxContent {
      margin-bottom: 10px;
      padding: 16px 5.37%;
      background: rgba(255, 255, 255, 1);
      border-radius: 8px;
      .label {
        display: inline;
        padding: 0.2em 0.6em 0.3em;
        font-size: 75%;
        font-weight: 700;
        line-height: 1;
        color: #03bb00;
        text-align: center;
        white-space: nowrap;
        vertical-align: baseline;
        border-radius: 9px;
        border: 1px solid rgba(3, 187, 0, 1);
      }
      .label.diy {
        color: #5d58f9;
        border: 1px solid #5d58f9;
      }
      h3 {
        font-size: 16px;
        font-weight: 500;
        color: rgba(61, 61, 61, 1);
        line-height: 26px;
        margin-bottom: 8px;
        em {
          font-size: 15px;
          font-weight: 500;
          color: #1c76fe;
          line-height: 21px;
          text-decoration: underline;
          float: right;
        }
      }
      p {
        font-size: 12px;
        font-weight: 500;
        color: rgba(189, 189, 189, 1);
        line-height: 17px;
      }
      div {
        margin-top: 18px;
        display: flex;
        flex-wrap: wrap;
        span {
          width: 33%;
          display: block;
          text-align: center;
          img {
            width: 32px;
            height: 32px;
            display: block;
            margin: 0 auto;
          }
          strong {
            margin-top: 5px;
            font-size: 14px;
            font-weight: bold;
            color: rgba(61, 61, 61, 1);
            line-height: 20px;
            display: block;
          }
          em {
            font-size: 12px;
            font-weight: 500;
            color: rgba(189, 189, 189, 1);
            line-height: 17px;
            display: block;
          }
        }
      }
    }
  }
}

.imgWrap {
  justify-content: space-between;
  .list {
    position: relative;
    width: calc((100% / 3) - 8px);
    display: block !important;
    text-align: center;
  }
  .img {
    width: 100%;
    padding-bottom: 100%;
    border-radius: 8px;
    position: relative;
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
      position: absolute;
      left: 0;
      right: 0;
      top: 0;
      bottom: 0;
    }
  }
  .txt {
    font-size: 12px;
    color: #3d3d3d;
    margin-top: 8px !important;
    justify-content: center;
  }
}
</style>
